<template>
  <div class="find-music">
    <nav class="find-type">
      <div :class="{checked:type===0}"
           @click="type = 0">
        个性推荐
      </div>
      <div :class="{checked:type===1}"
           @click="type = 1">
        歌单
      </div>
      <div :class="{checked:type===2}"
           @click="type = 2">
        主播电台
      </div>
      <div :class="{checked:type===3}"
           @click="type = 3">
        排行榜
      </div>
    </nav>
    <div class="carousel">
      <img :src="imageUrl[1]">
    </div>
    <div class="find-menu">
      <div @click="$router.push({name: 'songList', params:{id: 33}})">
        <div class="menu-circle">
          <i class="icon-FM">&#xe606;</i>
        </div>
        <div>私人FM</div>
      </div>
      <div @click="$router.push({name: 'songList', params:{id: 26}})">
        <div class="menu-circle">
          <i class="icon-tuijian">&#xe60e;</i>
        </div>
        <div>今日推荐</div>
      </div>
      <div @click="$router.push({name: 'songList', params:{id: 4}})">
        <div class="menu-circle">
          <i class="icon-grade">&#xe6c5;</i>
        </div>
        <div>排行榜</div>
      </div>
    </div>
    <div class="container">
      <div class="label">
        推荐歌单
        <i class="icon-larrow">&#xe60c;</i>
      </div>
      <div class="song-list">
        <ul>
          <li
            v-for="(item,i) in songList" @click="qqMusic(item.id)">
            <div class="list-item">
              <div>
                <img :src="item.url">
              </div>
              <div>自从听了这些音乐，敲代码越开越顺了</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <musicPlaying></musicPlaying>
  </div>
</template>

<script>
  import musicPlaying from '@/components/public/musicPlaying.vue';

  export default {
    name: "find-music",
    components: {
      musicPlaying
    },
    data() {
      return {
        type: 0,
        imageUrl: [
          "static/images/jay.jpg",
          "static/images/taylorswift.jpg",
        ],
        songList: [{
          url: "http://oiq8j9er1.bkt.clouddn.com/music_disconnected.jpg",
          id: 2646688496
        },{
          url: "http://oiq8j9er1.bkt.clouddn.com/music_%E6%88%91%E4%BB%A5%E4%B8%BA%E6%88%91%E5%8F%AF%E4%BB%A5.jpg",
          id: 1144416825
        },{
          url: "http://oiq8j9er1.bkt.clouddn.com/music_Leave%20Out%20All%20The%20Rest.jpg",
          id: 2043041547
        },{
          url: "http://oiq8j9er1.bkt.clouddn.com/music_disconnected.jpg",
          id: 2040362185
        },{
          url: "http://oiq8j9er1.bkt.clouddn.com/music_%E6%88%91%E4%BB%A5%E4%B8%BA%E6%88%91%E5%8F%AF%E4%BB%A5.jpg",
          id: 1723063372
        },{
          url: "http://oiq8j9er1.bkt.clouddn.com/music_Leave%20Out%20All%20The%20Rest.jpg",
          id: 3482605622
        }]
      }
    },
    methods:{
      qqMusic(id){
        window.location.href='https://y.qq.com/w/taoge.html?ADTAG=newyqq.taoge&id='+id;
      }
    }
  }
</script>

<style scoped>
  .find-music {
    padding-bottom: 2.5rem;
  }

  .find-type {
    height: 1.8rem;
    font-size: 14px;
    line-height: 1.8rem;
    overflow: hidden;
  }

  .find-type > div {
    height: 1.8rem;
    float: left;
    width: 25%;
    box-sizing: border-box;
    text-align: center;
  }

  .checked {
    color: #c62f2f;
    border-bottom: 2px solid #c62f2f;
  }

  .carousel {
    width: 100%;
    height: 8.2rem;
  }

  .carousel > img {
    width: 100%;
    height: 100%;
  }

  .find-menu {
    height: 4.5rem;
    overflow: hidden;
  }

  .find-menu > div {
    float: left;
    font-size: 12px;
    width: 33.3%;
    text-align: center;
  }

  .menu-circle {
    border: 1px solid #c62f2f;
    border-radius: 50%;
    width: 2.6rem;
    height: 2.6rem;
    margin: 0.6rem auto 0.2rem;
    line-height: 2.6rem;
    color: #c62f2f;
    font-size: 1.5rem;
  }

  .icon-FM {
    font-size: 2rem;
    color: #c62f2f;
  }

  .label {
    padding-left: 5px;
    border-left: 3px solid #c62f2f;
  }

  .icon-larrow {
    display: inline-block;
    transform: rotate(-90deg);
  }

  .song-list li {
    width: 32.6%;
    float: left;
    margin-right: 1%;
  }

  .song-list li:nth-of-type(3n) {
    margin-right: 0;
  }

  ul {
    overflow: hidden;
  }

  .list-item {
    margin-top: 0.4rem;
    font-size: 12px;
  }

  .list-item img {
    width: 100%;
  }
</style>



